.no-webp .imagen-contacto {
    background-image: url('../img/encuentra.jpg');
}

.webp .imagen-contacto {
    background-image: url('../img/encuentra.webp');
}

.imagen-contacto {
    background-position: center center;
    background-size: cover;
    padding: 5rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2 {
        font-weight: bold;
    }
}